.circular-refresh-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: transparent;
  border: none;
  color: var(--color-label-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: none;
  position: relative;
  overflow: hidden;
  outline: none;
}

.circular-refresh-btn:focus {
  outline: none;
  box-shadow: none;
}

.circular-refresh-btn:active {
  outline: none;
  box-shadow: none;
}

.circular-refresh-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.05);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 50%;
}

.circular-refresh-btn:hover::before {
  opacity: 0;
}

.circular-refresh-btn:hover {
  background: transparent;
  transform: none;
  box-shadow: none;
}

.circular-refresh-btn:disabled {
  opacity: 0.4;
  cursor: default;
  transform: none !important;
}

.circular-refresh-btn:disabled:hover {
  background: transparent;
  transform: none;
  box-shadow: none;
}

.circular-refresh-btn:disabled:hover::before {
  opacity: 0;
}

